import {mapState} from 'vuex'
import uniCollapse from '../../components/uni-collapse/uni-collapse.vue'
import uniCollapseItem from '../../components/uni-collapse-item/uni-collapse-item.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
export default {
	data() {
		return {
			getBook: false,//找到书籍
			getCode: false,//校验二维码成功
			Rlist: [],//记录实体list
			Blist: [],//书编号list
			code: '',//二维码信息
			OverdueCount: 0 //过期记录个数
		}
	},
	computed:{
		...mapState([ 'skey','student']),
		student: {
			get() {
				return this.$store.state.student;
			},
			set(val) {
				this.$store.state.student = val
			}
		}
	},
	onShow() {
		var _self = this;
		// _self.$options.methods.rellist(_self)
	},
	
	methods: {
		checkCode(){
			var _self = this;
			//扫码，初步校验，传到后台校验
			uni.scanCode({
				scanType: 'qrCode',
				success(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					uni.request({
						url: 'http://192.168.31.8:8081/checkCode',
						method: "POST",
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						data: {
							code: res.result
						},
						success(res1) {
							console.log(res1.data);
							if(res1.data.msg=="OK"){
								_self.getCode= true;
								console.log("res.result="+res.result);
								_self.code =res.result;
								uni.showToast({
									duration: 1000
								})
							}
							else{
								uni.showToast({
									icon: "none",
									title: "二维码错误"
								})
							}
						}
					})
				}
			})
		},
		checkBook(){
			var _self = this
			//查找是否有该书未还记录
			//验证条形码
			uni.scanCode({
			    scanType: 'barCode',
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
					//检验是否为isbn
					var regex = /^(?:ISBN(?:-1[03])?:? )?(?=[0-9X]{10}$|(?=(?:[0-9]+[- ]){3})[- 0-9X]{13}$|97[89][0-9]{10}$|(?=(?:[0-9]+[- ]){4})[- 0-9]{17}$)(?:97[89][- ]?)?[0-9]{1,5}[- ]?[0-9]+[- ]?[0-9]+[- ]?[0-9X]$/;
					if(regex.test(res.result)){
						// 进入借书流程
						_self.$options.methods.checkReturn(_self,res.result);
					}else{
						uni.showToast({
							icon: "none",
							title:"无法识别此条码"
						})
					}
			    }
				
			})
		},
		checkReturn(_self,isbn){
			uni.request({
				url: 'http://192.168.31.8:8081/checkReturn',
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					isbn: isbn,
					skey: _self.skey
				},
				success(res) {
					console.log(res.data);
					if(res.data.msg=="OK"){
						let flag = 0;
						//检查重复
						for(let i=0 ;i<_self.Blist.length ;i++){
							if(_self.Blist[i] == res.data.data.isbn){
								flag = flag + 1;
								}
							console.log(flag);
						}
						if(flag == 0){
							_self.Rlist.push(res.data.data);
							_self.getBook = true;
							_self.Blist.push(res.data.data.isbn)
							console.log("_self.Rlist="+_self.Rlist);
							console.log("_self.Blist="+_self.Blist);
							 if(res.data.data.isOverdue == 1){
								 _self.OverdueCount ++;
								 uni.showToast({
								 	icon:"none",
								 	title:"该图书已经逾期"
								 })
							 }
						}else{
							console.log("flag="+flag); 
							uni.showToast({
								icon:"none",
								title:"该图书已在归还列表中"
							})
						}
					}else{
						uni.showToast({
							icon:"none",
							title: res.data.msg
						})
					}
				}
			})
		},
		submitReturn(){
			var _self = this
			//提交blist
			uni.request({
				url: 'http://192.168.31.8:8081/submitReturn',
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					Blist: _self.Blist,
					skey: _self.skey,
					code: _self.code
				},
				success(res) {
					console.log("res.data="+res.data);
					if(res.data.msg=="OK"){
						//更新积分
						//获得返回学生
						_self.student=res.data.data
						//刷新当前
						_self.getBook = false;
						_self.Blist = [];
						_self.Rlist = [];
						//显示成功
						uni.showToast()
						if(_self.OverdueCount > 0){
							uni.showModal({
								title: "您有借阅的图书已经逾期",
								content: "需要缴纳"+_self.OverdueCount+"元",
								showCancel: false,
								success: function(res) {
									if (res.confirm) {
										console.log('用户点击确定');
										// uni.navigateTo({
										// 	url: ""
										// })
										console.log("缴纳");
									} 
								}
							})
						}
					}else{
						uni.showToast({
							icon:"none",
							title:res.data.msg
						})
						//刷新当前
						_self.getBook = false;
						_self.Blist = [];
						_self.Rlist = [];
					}
				}
			})
		},
		
	}
}
